<template>
    <ul class="tc_daycheck">
        <li v-for="(item, index) in dayList">
            <span>
                <input type="checkbox" v-model="item.check" :id="name + item.id" :key="name + item.id" @change="checkChange">
                <label :class="{on: item.check}" :for="name + item.id">
                    <span class="label_icon"></span>
                    <span class="label_txt">{{item.str}}</span>
                </label>
            </span>
        </li>
    </ul>
</template>
<script>
    import baseComponent from './../base';

    export default {
        extends: baseComponent,
        name: 'daycheck',
        computed : {
            value: {
                get: function() {
                    const valueList = [];
                    this.dayList.forEach((value) => {
                        valueList.push(value.check);
                    });
                    return valueList;
                },
                set: function(newValues) {
                    if (!newValues) {
                        return;
                    }
                    let i, len = newValues.length;
                    for (i = 0; i < len; i+=1) {
                        if (this.dayList[i]) {
                            this.dayList[i].check = newValues[i];
                        }
                    }
                }
            }
        },
        data: function() {
            return {
                name: 'name_' + this.parentId,
                dayList: []
            }
        },
        created : function() {
            const lang = document.documentElement.getAttribute('lang');
            this.dayList = [
                {id: 'MON', str: lang === 'ja' ? '' : '', check: false},
                {id: 'TUE', str: lang === 'ja' ? '' : '', check: false},
                {id: 'WED', str: lang === 'ja' ? '' : '', check: false},
                {id: 'THU', str: lang === 'ja' ? '' : '', check: false},
                {id: 'FRI', str: lang === 'ja' ? '' : '', check: false},
                {id: 'SAT', str: lang === 'ja' ? '' : '', check: false},
                {id: 'SUN', str: lang === 'ja' ? '' : '', check: false}
            ];
        },
        methods: {
            checkChange: function() {
                const valueList = [];
                this.dayList.forEach((value) => {
                    valueList.push(value.check);
                });
                this.emitEvent('changed', valueList);
            }
        }
    }
</script>
<style lang="less">
    .tc_daycheck {
        input {
            opacity: 0;
        }
        li {
            display:inline-block;
            position:relative;
            width:40px;
            height:30px;
            margin-right:5px;
        }
        label {
            cursor:pointer;
            .label_icon {
                position: absolute;
                width: 32px;
                height: 32px;
                top: 0px;
                left: 0px;
                background-color: #ffffff;
                border: solid 1px #cccccc;
                border-radius: 32px;
            }
            .label_txt {
                position: absolute;
                z-index: 10;
                left:11px;
                top:8px;
                font-size:14px;
            }
            &.on {
                .label_icon {
                    background-color: #767d96;
                }
                .label_txt {
                    color: #ffffff;
                }
            }
        }
    }
</style>


